import {AUTH,SET_AUTH,UNSET_AUTH} from '../_types'

const namespace = AUTH;
const initState = {
  authenticated: !!localStorage.getItem('user')
}

export default function auth(state = initState, {type, payload}) {
  // CHECK NAMESPACE
  if (type.indexOf(namespace)!==0) return state;
  type = type.replace(namespace, '');

  if (type === SET_AUTH){
    state.authenticated = true;
  }
  if (type === UNSET_AUTH) {
    state.authenticated = false;
  }
  return Object.assign({}, state);
}