import React from 'react';
import {connect} from "react-redux";
import Notification from "arui-feather/notification";
import {pushNotification, removeNotification} from "../_store/notices";

class GlobalNotifications extends React.Component {

	closeNotification(id) {
		this.props.removeNotification(id);
	}

	render() {
		const notifications = this.props.notifications;
		const offsets = {left: 0, right: 0};
		return (
			<div className="global-notifications p-fixed color-white" style={{top:0, left: 0, zIndex: 10000}}>
				{notifications.map(({id, message = '', options = {}}, index) => {
					const pos = options.stickTo ? options.stickTo : 'right';
					options.autoCloseDelay = options.autoCloseDelay ? options.autoCloseDelay : 5000;
					const notification = (
						<Notification
							ref={r => {return notifications[index] ? notifications[index].ref = r : ''}}
							key={id}
							visible={ true }
							offset={ offsets[pos] + 10 }
							stickTo={pos}
							onCloseTimeout={ () => { this.closeNotification(id) }}
							onCloserClick={ () => { this.closeNotification(id) }}
							{...options}
						>
							{message}
						</Notification>
					);
					offsets[pos] += notifications[index].ref ? notifications[index].ref.root.offsetHeight + 10 : 0;
					return notification;
				})}
			</div>
		)
	};
}

export default connect(
	state => ({
		notifications: state.notices.notifications
	}),
	dispatch => ({
		removeNotification: (payload) => {
			dispatch(removeNotification(payload));
		},
		pushNotification: notification => dispatch(pushNotification(notification))
	})
)(GlobalNotifications);
