import axios from 'axios';
import {userService} from '../_services/user.service'
import {TOKEN_EXPIRED} from "../_types";

const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : false;
const token = user ? user.token_type + ' ' + user.access_token : '';
const lastActivity = localStorage.getItem('last-activity') ? new Date(localStorage.getItem('last-activity')) : new Date(0);
const baseURLS = {
  'development': 'http://localhost:8080/api',
  'production': '/api',
};
const client =  axios.create({
  baseURL: baseURLS[process.env.NODE_ENV] || 'http://localhost:8080/api',
	headers: {
		'Content-Type': 'application/json',
    'Authorization': token
	}
});

client.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  if (error && error.response && error.response.status === 401) {
    console.log(token);
    console.log("token expired");
    if (new Date() - lastActivity.getTime() > 30 * 60 * 1000) {
      userService.logout(TOKEN_EXPIRED);
    }
    else if (user){
      const credentials = window.atob(user.authdata).split(':');
      userService.login(credentials[0], credentials[1]).then(user => console.log(user));
    }
  }
  return Promise.reject(error);
});

export default client;
