import React from 'react';
import PromptConfirmationModal from "arui-private/prompt-confirmation-modal";
import {connect} from "react-redux";

function GlobalConfirms({confirms=[]}) {
  
  return (
    <div className="global-confirms">
      {
        confirms.map(confirm => (
          <PromptConfirmationModal
            key={ confirm.id }
            {...confirm}
          />
        ))
      }
    </div>
  )
}

function mapStateToProps(state) {
  return {
    confirms: state.notices.confirms
  }
}

export default connect(mapStateToProps)(GlobalConfirms);
