import {
  FILTER_DAY_TODAY,
  SORT_CREATED_AT,
  SORT_DESC,
  SORT_EDITED_AT, STATUS_DRAFT,
  STATUS_PENDING,
  STATUS_SUCCESS,
  STATUS_WRONG,
} from '../_types'
import requestMock from "./requestMock";
import {isSameDay} from './dates'

const FIXED_STATUSES = [
  STATUS_SUCCESS, STATUS_PENDING
];

export const mockApplications = {
  localStorageNaming: 'application_list',
  generateId(list = []) {
    let text = "";
    const possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    const check = (item) => item.id === text;
    while(true) {
      text = "";
      for (let i = 0; i < 15; i++)
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      if (!list.find(check)) break;
    }
    return text;
  },
  fetchApplications({limit=10, offset=0, sortBy=SORT_CREATED_AT, sortOrder=SORT_DESC}, {search='', day='', status=[]}) {
    return new Promise((resolve) => {
      requestMock(() => {
        let applications = this.getApplications();
        applications = applications.filter(application => {
          let bool = true;
          if (search && search !== '') {
            search = search.toLowerCase();
            bool = application.name.toLowerCase().indexOf(search)!==-1 ||
              application.code.split().join('').indexOf(search.split().join(''))!==-1 ||
              application.contactName.toLowerCase().indexOf(search)!==-1 ||
              application.phone.toLowerCase().indexOf(search)!==-1 ||
              application.additionalPhone.toLowerCase().indexOf(search)!==-1;
          }
          if (day === FILTER_DAY_TODAY) {
            bool = bool && isSameDay(application.createdAt);
          }
          if (status.length > 0) {
            bool = bool && status.indexOf(application.status) !== -1;
          }
          return bool;
        });
        applications.sort((a,b) => {
          if (sortBy === SORT_CREATED_AT) {
            return a.createdAt.getTime() - b.createdAt.getTime();
          } else if (sortBy === SORT_EDITED_AT) {
            return a.updatedAt.getTime() - b.updatedAt.getTime();
          }
          return 1;
        });
        if (sortOrder === SORT_DESC) {
          applications.reverse()
        }
        resolve({ applications: applications.slice(offset, offset+limit), total: applications.length})
      })
    });
  },
  getApplications() {
    let applications = [];
    try {
      applications = JSON.parse(window.localStorage.getItem(this.localStorageNaming));
      if (!applications) applications = [];
    } catch (e) {
      console.error(e);
      applications = [];
    }
    applications.forEach((application, i) => {
      application.createdAt = new Date(application.createdAt);
      application.updatedAt = application.updatedAt ? new Date(application.updatedAt) : application.createdAt;
      if(!application.id) {
        application.id = this.generateId([]);
      }
      const delay = 1000 * 60;
      if (!application.history) application.history = [];
      if (!application.status || application.status === STATUS_PENDING) {
        if ((new Date()).getTime() - application.updatedAt.getTime() > delay) {
          application.status = Math.random() > .7 ? STATUS_WRONG : STATUS_SUCCESS;
          application.history.push({
            status: application.status,
            time: new Date(),
            author: 'Карина Олеговна',
            application: JSON.stringify(application)
          });
        }
      }
      application.history.forEach(e => {
        e.time = new Date(e.time);
      });
      application.editable = !FIXED_STATUSES.includes(application.status);
    });
    window.localStorage.setItem(this.localStorageNaming, JSON.stringify(applications));
    return applications.slice();
  },
  editApplication(id, a, user) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const application = Object.assign({}, a);
        const applications = this.getApplications();
        const index = applications.findIndex(ap => ap.id === application.id);
        if (index===-1) {
          reject({message: 'Данного объекта не существует, либо он был удален'});
          return;
        }
        if (!applications[index].editable) {
          reject({message: 'Данный объект не изменяем'});
          return;
        }
        application.updatedAt = new Date();
        if (application.status !== STATUS_DRAFT) application.status = STATUS_PENDING;
        application.history.push({
          status: application.status,
          time: new Date(),
          author: user.firstName + ' ' + user.lastName,
          application: JSON.stringify(application)
        });
        application.editable = !FIXED_STATUSES.includes(application.status);
        applications[index] = application;
        localStorage.setItem(this.localStorageNaming, JSON.stringify(applications));
        resolve(application);
        
      }, 1000)
    });
  },
  addApplication(a, user) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const application = Object.assign({}, a);
        const applications = this.getApplications();
        if (!application.id) application.id = this.generateId(applications);
        if (!application.createdAt) application.createdAt = new Date();
        application.updatedAt = new Date();
        if (application.status !== STATUS_DRAFT) {
          application.status = STATUS_PENDING;
        }
        application.history = [{
          status: application.status,
          time: new Date(),
          author: user.firstName + ' ' + user.lastName,
          application: {...application}
        }];
        application.editable = !FIXED_STATUSES.includes(application.status);
        applications.push(application);
        localStorage.setItem(this.localStorageNaming, JSON.stringify(applications));
        resolve(application);
      }, 1000);
    })
  },
  removeApplication(a) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const application = Object.assign({}, a);
        const applications = this.getApplications().slice();
        const index = applications.findIndex(ap => ap.id === application.id);
        if (index===-1) {
          reject({message: 'Данного объекта не существует, либо он уже был удален'});
          return;
        }
        if (!applications[index].editable) {
          reject({message: 'Невозможно удалить данный объект'});
          return;
        }
        const newApplications = applications.slice();
        newApplications.splice(index, 1);
        localStorage.setItem(this.localStorageNaming, JSON.stringify(newApplications));
        resolve(application);
      }, 1000)
    })
  },
  getApplication(id) {
    return this.getApplications().find(a => a.id === id)
  },
  fetchApplication(id) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const app = this.getApplication(id);
        if (app) {
          resolve(app);
        } else {
          reject({message: 'Заявки с заданным id не существует или был удален'})
        }
      }, 1000)
    })
  }
};

