import {ADD_CONFIRM, ADD_NOTIFICATION, NOTICES, REMOVE_CONFIRM, REMOVE_NOTIFICATION} from "../_types";

const namespace = NOTICES;
const initState = {
	notifications: [],
	confirms: [
		// {
		// 	id: '0',
		// 	confirmButtonLabel: 'Да',
		// 	cancelButtonLabel: 'Нет',
		// 	header: '',
		// 	isProcessing: false,
		// 	visible: true,
		// 	text: <Label>Привет</Label>,
		// 	onCancelClick() {},
		// 	onConfirmClick() {}
		// },
		// {
		// 	id: '1',
		// 	confirmButtonLabel: 'Да',
		// 	cancelButtonLabel: 'Нет',
		// 	header: '',
		// 	isProcessing: false,
		// 	visible: true,
		// 	text: <Label>Good morning</Label>,
		// 	onCancelClick() {},
		// 	onConfirmClick() {}
		// }
	]
};

export default function notices(state = initState, {type, payload}) {
	// CHECK NAMESPACE
	if (type.indexOf(namespace)!==0) return state;
	type = type.replace(namespace, '');

	if (type === ADD_NOTIFICATION) {
		const newState = Object.assign({}, state);
		payload.id = new Date().getTime().toString() + newState.notifications.length;
		newState.notifications =  [...newState.notifications, payload];
		return newState;
	}

	if (type === REMOVE_NOTIFICATION) {
		const notifications = Object.assign([], state.notifications);
		const index = notifications.findIndex(n => n.id === payload);
		if (index!==-1) {
			notifications.splice(index,1);
		}
		return {...state, notifications};
	}
	
	if (type === ADD_CONFIRM) {
		const newState = Object.assign({}, state);
		payload.id = new Date().getTime().toString() + newState.confirms.length;
		payload.onCancelClick = () => { payload.rejection(payload.id) };
		payload.onConfirmClick = () => { payload.resolving(payload.id) };
		newState.confirms =  [...newState.confirms, payload];
		return newState;
	}
	
	if (type === REMOVE_CONFIRM) {
		const confirms = Object.assign([], state.confirms);
		const index = confirms.findIndex(n => n.id === payload);
		if (index!==-1) {
			confirms.splice(index,1);
		}
		return {...state, confirms};
	}
	
	return {...state};
};

export const pushNotification = payload => dispatch => {
	dispatch({type: namespace + ADD_NOTIFICATION, payload});
};

export const removeNotification = payload => dispatch => {
	dispatch({type: namespace + REMOVE_NOTIFICATION, payload});
};

export const pushConfirm = payload => dispatch => {
	dispatch({type: namespace + ADD_CONFIRM, payload});
};

export const removeConfirm = payload => dispatch => {
	dispatch({type: namespace + REMOVE_CONFIRM, payload})
};
