import {
  ALREADY_FETCHED,
  CLEAR,
  DELAY_NOT_PASSED,
  ERROR,
  LOADING,
  REMOVE_USER,
  SET_REQUEST_STATUS,
  SET_USER, SET_USERFULLNAME,
  SET_USERNAME,
  SUCCESS,
  USER,
} from '../_types'
import client from '../_api'

const namespace = USER;
const REQUEST_DELAY = 30 * 1000;
const initState = {
  request: CLEAR,
  lastRequest: null,
  user: {},
  username: localStorage.getItem('user') ? window.atob(JSON.parse(localStorage.getItem('user')).authdata).split(':')[0] : '',
  user_full_name: localStorage.getItem('user_full_name'),
};

export default function user(state = initState, {type, payload}) {
  // CHECK NAMESPACE
  if (type.indexOf(namespace)!==0) return state;
  type = type.replace(namespace, '');

  if (type === SET_REQUEST_STATUS) {
    state.request = payload;
    state.lastRequest = new Date();
  }
  if (type === SET_USER){
    state.user = Object.assign({}, payload);
  }
  if (type === SET_USERNAME) {
    state.username = payload;
  }
  if (type === SET_USERFULLNAME) {
    state.user_full_name = payload;
  }
  if (type === REMOVE_USER) {
    state = initState;
  }
  return Object.assign({}, state);
}

export const fetchUserData = (username, {light = false, forced = false}) => (dispatch, getState) => new Promise((resolve, reject) => {
  const {lastRequest, request} = getState().user;
  if (light && !forced && lastRequest && request===SUCCESS) {
    throw new Error(ALREADY_FETCHED);
  }
  if (lastRequest && !forced &&  new Date().getTime() - lastRequest.getTime() < REQUEST_DELAY ) {
    throw new Error(DELAY_NOT_PASSED);
  }
  dispatch({type: namespace + SET_REQUEST_STATUS, payload: LOADING});
  client.get('profile?username=' + username).then(({data: user}) => {
    dispatch({type: namespace + SET_USER, payload: user});
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: SUCCESS});
  }).catch(err => {
    console.log(err);
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
    reject(err);
  })
});

export const changePassword = (newPassword) => (dispatch, getState) => new Promise((resolve, reject) => {
  const username = getState().user.username;
  console.log({username, newPassword});
  client.post('changePassword', {username, newPassword}).then((res) => {
    console.log(res);
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: SUCCESS});
    resolve(res);
  }).catch(err => {
    console.log(err);
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
    reject(err);
  })
});

