import React, {lazy, Suspense} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom'
import PrivateRoute from './components/PrivateRoute';
import {userService} from './_services/user.service';
import LazyComponent from "./components/LazyComponent";
import {USER_LOGOUT} from "./_types";
import GlobalNotices from "./components/GlobalNotices";

const LoginPage = lazy(() => import("./pages/LoginPage/LoginPage"));
const NotFound = lazy(() => import("./pages/NotFound"));
const DashboardPage = lazy(() => import("./pages/DashboardPage/DashboardPage"));


function App() {
	return (
		<div className="app">
			<Suspense fallback={<div>Loading...</div>}>
				<Switch>
					<Route path="/login" exact component={LazyComponent(LoginPage)}/>
					<Route path="/logout" render={() => {
						userService.logout(USER_LOGOUT);
						return <Redirect to="/dashboard"/>;
					}}/>
					<PrivateRoute path="/dashboard" component={DashboardPage}/>
					<Route path="/" exact render={()=> <Redirect to="/dashboard"/> }/>
					<Route path='/404' exact component={LazyComponent(NotFound)} />
					<Redirect from='*' to='/404' />
				</Switch>
			</Suspense>
			<GlobalNotices/>
		</div>
	)
}

export default App;
