import {ADD_PARTNER, CLEAR, PARTNERS, SET_REQUEST_STATUS} from "../../_types/index";

const namespace = PARTNERS;
const initState = {
	request: CLEAR,
	list: [],
}

export default function partners(state = initState, {type, payload}) {
	// CHECK NAMESPACE
	if (type.indexOf(namespace)!==0) return state;
	type = type.replace(namespace, '');

	if (type === SET_REQUEST_STATUS) {
		state.request = payload
	}
	if (type === ADD_PARTNER){
		state.list.push(payload)
	}
	return state;
}