import client from '../_api'
import {BAD_CREDENTIALS, NO_DATA} from '../_constants'
import store from '../_store';
import {
  AUTH,
  REMOVE_USER,
  SET_AUTH, SET_USERFULLNAME,
  SET_USERNAME,
  TOKEN_EXPIRED,
  UNSET_AUTH,
  USER, USER_LOGOUT,
} from '../_types'
import {pushNotification} from "../_store/notices";
import {mockAuth} from "../_api/mock-server";

export const userService = {
  login,
  logout
};


async function login(username, password) {
  return await mockAuth.checkPassword(username, password)
    .catch(handleError)
    .then(handleSuccess)
    .then(user => {
      if (user) {
        console.log(user);
        user.authdata = window.btoa(username + ':' + password);
        localStorage.setItem('user', JSON.stringify(user));
        localStorage.setItem('user_full_name', user.firstName);
        client.defaults.headers['Authorization'] = user.token_type + ' ' + user.access_token;
        store.dispatch({type: AUTH+SET_AUTH});
        store.dispatch({type: USER+SET_USERNAME, payload: username});
        store.dispatch({type: USER+SET_USERFULLNAME, payload: user.firstName});
      }
      return user;
    });
}

function logout(reason) {
  console.log('logout');
  localStorage.removeItem('user');
  delete client.defaults.headers.common['Authorization'];
  delete client.defaults.headers['Authorization'];
  store.dispatch({ type: USER + REMOVE_USER });
  store.dispatch({ type: AUTH + UNSET_AUTH  });
  if (reason === TOKEN_EXPIRED) {
    store.dispatch(pushNotification({message: "Время сессии истекло", options: {status: 'error', title: 'Выход из системы'}}))
  }
  if (reason === USER_LOGOUT) {
    store.dispatch(pushNotification({message: "Выход из системы выполнен успешно", options: {status: 'ok', title: 'Выход из системы'}}))
  }
}

function handleSuccess(res) {
  if (res.data) return res.data;
  return Promise.reject({data: NO_DATA})
}

function handleError({response: {status, data}}) {
  const errors = {};
  if(status === 400 && data.error_description === BAD_CREDENTIALS)
    errors.badCredentials = {status, data: BAD_CREDENTIALS};
  return Promise.reject(errors)
}
