import {
  ADD_APPLICATION, ALREADY_FETCHED,
  APPLICATIONS,
  CLEAR,
  DELAY_NOT_PASSED,
  REMOVE_APPLICATION,
  SET_REQUEST_STATUS, SORT_CREATED_AT, SORT_DESC,
} from '../_types/index'
import {
  EDIT_APPLICATION,
  ERROR,
  FETCH_APPLICATIONS,
  LOADING, SET_FILTERS,
  SET_LAST_REQUEST, SET_PARAMS,
  SUCCESS,
} from '../_types'
import {mockApplications} from "../_helpers/mock-server";

const namespace = APPLICATIONS;
const REQUEST_DELAY = 6 * 1000;
const initState = {
	request: CLEAR,
	lastRequest: null,
	params: {
		total: 0,
		limit: 10,
		offset: 0,
		sortBy: SORT_CREATED_AT,
		sortOrder: SORT_DESC
	},
	filters: {
		search: '',
		day: '',
		status: []
	},
	sortBy: null,
	list: [],
};

export default function applications(state = initState, {type, payload}) {
	// CHECK NAMESPACE
	if (type.indexOf(namespace)!==0) return state;
	type = type.replace(namespace, '');

	if (type === SET_LAST_REQUEST) {
		state.lastRequest = new Date();
	}
	if (type === SET_REQUEST_STATUS) {
		state.request = payload;
	}

	if (type === FETCH_APPLICATIONS) {
		state.list = payload;
	}
	if (type === SET_PARAMS) {
		state.params = {...state.params, ...payload};
	}
	if (type === SET_FILTERS) {
		state.filters = {...state.filters, ...payload};
	}
	
	if (type === ADD_APPLICATION){
		state.list = [payload, ...state.list];
	}

	if (type === EDIT_APPLICATION){
		const index = state.list.findIndex(a => a.id === payload.id);
		state.list[index] = payload;
	}

	if (type === REMOVE_APPLICATION) {
		const index = state.list.findIndex(v => v.id === payload);
		const newList = state.list.slice();
		newList.splice(index, 1);
		state.list = newList.slice();
	}
	return Object.assign({}, state);
}


// ---- ACTIONS ---- //
export const addApplication = (application, user) => dispatch => new Promise((resolve, reject) => {
	dispatch({type: namespace + SET_REQUEST_STATUS, payload: LOADING});
 
	let promise;
	if (application.id) {
    promise = mockApplications.editApplication(application.id, application, user).then(application => {
      dispatch({type: namespace + EDIT_APPLICATION, payload: application});
      return application;
    });
  } else {
    promise = mockApplications.addApplication(application, user).then((application) => {
      dispatch({type: namespace + ADD_APPLICATION, payload: application});
      return application;
    });
  }
  promise.then(application => {
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: SUCCESS});
    resolve(application);
  }).catch(err => {
    dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
    reject(err);
  })
});

export const removeApplication = application => dispatch => new Promise((resolve, reject) => {
	mockApplications.removeApplication(application).then(app => {
		dispatch({type: namespace + REMOVE_APPLICATION, payload: app.id});
		resolve(app.id);
	}).catch((err) => {
		dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
		reject(err.message);
	})
});

export const getApplications = ({light, forced, silent} = {light: false, forced: false, silent: false}) => (dispatch, getState) => new Promise((resolve, reject) => {
  const {lastRequest, request, params, filters} = getState().applications;
  if (light && !forced && lastRequest && request===SUCCESS) {
    reject({message: ALREADY_FETCHED});
    return;
  }if (lastRequest && !forced &&  new Date().getTime() - lastRequest.getTime() < REQUEST_DELAY ) {
		reject({message: DELAY_NOT_PASSED, data: lastRequest});
		return;
	}
	if(!silent) dispatch({type: namespace + SET_REQUEST_STATUS, payload: LOADING});
	return mockApplications.fetchApplications(params, filters).then(({applications, total}) => {
		dispatch({type: namespace + FETCH_APPLICATIONS, payload: applications.slice()});
		dispatch({type: namespace + SET_PARAMS, payload: {total}});
		if(!silent)dispatch({type: namespace + SET_REQUEST_STATUS, payload: SUCCESS});
		dispatch({type: namespace + SET_LAST_REQUEST});
		resolve({applications: applications.slice(), total})
	}).catch(err => {
		if(!silent)dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
		reject(err);
	})
});
