import {ADD_CLIENT, CLEAR, CLIENTS, SET_REQUEST_STATUS} from "../_types/index";
import {ERROR, LOADING, SUCCESS} from "../_types";

const namespace = CLIENTS;
const initState = {
	request: CLEAR,
	list: [],
}

export default function clients(state = initState, {type, payload}) {
	// CHECK NAMESPACE
	if (type.indexOf(namespace)!==0) return state;
	type = type.replace(namespace, '');

	if (type === SET_REQUEST_STATUS) {
		state.request = payload
	}

	if (type === ADD_CLIENT){
		state.list.push(payload)
	}
	return state;
}



// ---- ACTIONS ---- //
export const addClient = client => dispatch => new Promise((resolve, reject) => {
	dispatch({type: namespace + SET_REQUEST_STATUS, payload: LOADING});
	setTimeout(() => {
		console.log(!client.bin);
		if(client.bin) {
			dispatch({type: namespace + ADD_CLIENT, payload: client});
			dispatch({type: namespace + SET_REQUEST_STATUS, payload: SUCCESS});
			resolve(client);
		} else {
			dispatch({type: namespace + SET_REQUEST_STATUS, payload: ERROR});
			reject(new Error('Some error happened'));
		}
	}, 700);
});
